
import {createSocket} from "./socket.js"
import {isCardTrump, sortHand} from "./euchreHelpers.js"

function createStore(reducer, initialState) {
  let state = initialState;
  const listeners = [];

  const subscribe = (listener) => (
    listeners.push(listener)
  );

  const getState = () => (state);

  const dispatch = (action) => {
    state = reducer(state, action);
    listeners.forEach(l => l());
  };

  return {
    subscribe,
    getState,
    dispatch,
  };
}

function reducer(s, action) {
  
  let state = Object.assign({}, s);

  if(action.type === 'LOG_IN'){


    state.socket = createSocket("wss://freethy-euchre.tk", "chat");


    state.socket.onopen( ()=> {
      const msg = {
        action:"LOG_IN",
        name:state.myName
      }
      state.socket.send(JSON.stringify(msg));
    });
    return state;


  }else if(action.type === 'CHAT_MESSAGE'){
    const msg = {
      action:"CHAT_MESSAGE",
      name:state.myName,
      message:action.message
    }
    
    state.socket.send(JSON.stringify(msg));
    return state;

  }else if(action.type === 'TOGGLE_CHAT'){
    state.isChatOpen = !state.isChatOpen;
    state.newChat = false;
    return state

  }else if(action.type === 'BACK_TO_MENU'){
    state.inGame = false;
    state.players = [{},{},{},{}];
    state.gameScores={t1:0,t2:0};
    state.seatNum = null;
    state.hand=[];

    return state;
    
  }else if(action.type === 'CREATE_LOBBY'){
  
    const msg = {
      action: "CREATE_LOBBY",
      lobbyName: action.lobbyName
    };
    state.socket.send(JSON.stringify(msg));
    
    return state;
  
  } else if (action.type === 'PLAYER_NAME_CHANGE'){
    state.myName = action.name;
    return state;


  } else if (action.type === 'SIT_DOWN'){
    const msg = {
      action:"SIT_DOWN",
      seatNum:action.seatNum,
      lobbyName:action.lobbyName
    };
    state.socket.send(JSON.stringify(msg));
    state.winString = "";
    state.inGame = true;
    state.afterGame = false;
    state.mySeat = parseInt(action.seatNum,10);
    return state;

  } else if (action.type === 'STAND_UP'){
    state.mySeat=null;
    const msg = {
      action:"STAND_UP",
      seatNum:action.seatNum
    }
    state.socket.send(JSON.stringify(msg))
    return state

  } else if (action.type === 'START_GAME'){
    const msg = {
      action:"START_GAME"
    }
    
    state.socket.send(JSON.stringify(msg))
    return state;

  } else if (action.type === 'RESTART_GAME'){
    const msg = {
      action:"RESTART_GAME"
    }
    state.socket.send(JSON.stringify(msg));
    return state;

  } else if (action.type === "PASS") {
    const msg = {
      action: "PASS",
      seatNum: state.whosTurn
    }
    state.socket.send(JSON.stringify(msg))

    return state
  } else if (action.type === "ORDER_UP") {
    const msg= {
      action:"ORDER_UP",
      seatNum:state.whosTurn,
      isAlone: action.isAlone
    }
    state.socket.send(JSON.stringify(msg))
    return state;


  } else if (action.type === "DEALER_DISCARD") {

    for(let i = 0; i<state.hand.length; i++){
      if(state.hand[i].id === action.card){
        state.hand.splice(i, 1)
      }
    }
    state.offerCard = {};
    const msg = {
      action:"DEALER_DISCARD",
      card:action.card
    }
    state.hand = sortHand(state.hand, state.trump)
    state.sortingHand = true;
    setTimeout(()=>{store.dispatch({type:"DONE_SORTING"})}, 1000);
    state.socket.send(JSON.stringify(msg))
    return state;


  } else if (action.type === "CALL_TRUMP") {
    const msg = {
      action:"CALL_TRUMP",
      trump: action.trump,
      seatNum: state.whosTurn,
      isAlone: action.isAlone
    }
    
    state.socket.send(JSON.stringify(msg))
    return state;



  } else if (action.type === "DONE_SORTING") {
    state.sortingHand = false;
    return state;

  } else if (action.type === "PLAY_CARD") {
    if(!state.hasPlayedCard){
      state.hasPlayedCard = true;
      const msg = {
        action:"PLAY_CARD",
        card:action.card,
        seatNum:state.mySeat
      }
      
      state.socket.send(JSON.stringify(msg))
    }
    
    return state;
  } else if (action.type === "WIN_TRICK_ANIMATION") {
    const locationMap = {1:"bottom", 2:"left", 3:"top", 4:"right"}
    state.winnerLocation = locationMap[((parseInt(action.winner, 10) - state.mySeat + 4) % 4) + 1];

    setTimeout(()=>{
      store.dispatch({
        type:"CLEAR_TRICK_CARDS",
        whosTurn:action.winner
      })
    },700);

    return state;
  } else if (action.type === "CLEAR_TRICK_CARDS") {
    // console.log(action)
    state.whosTurn = action.whosTurn;
    state.winnerLocation=null;
    state.trickCards=[];
    state.trickNumber++;
    return state;
  }








  








  // ----------------------------------------
  //        Incoming Socket Activity
  // ----------------------------------------

  else if (action.type === 'SOCKET_MESSAGE'){

    let data = action.data;


    if(data.action === "LOG_IN"){
      state.loggedIn = true;
      state.lobbies = data.lobsters;

    }else if(data.action === "CREATE_LOBBY"){
      if(!data.result === "success"){
        alert(data.error)
      } 


    } else if(data.action === "UPDATE_LOBBIES"){
      state.lobbies = data.lobsters;
      for(let i=0; i<data.lobsters.length; i++){
        for(let j=0; j < data.lobsters[i].players.length; j++){
          state.lobbies[i].players[j].seat = parseInt(state.lobbies[i].players[j].seat,10)
        }
      }

    } else if(data.action === "SIT_DOWN"){
      state.players = [{},{},{},{}];
      for(let i=0; i<data.players.length; i++){
        state.players[data.players[i].seat-1] = data.players[i]
        state.players[data.players[i].seat-1].seat = parseInt(data.players[i].seat, 10);
      }

    } else if(data.action === "START_HAND"){
      state.afterGame = false;
      state.gameStarted = true;
      state.whosDealer = parseInt(data.dealer, 10);
      state.whosTurn = parseInt(data.dealer, 10)%4 + 1;
      state.offerCard = data.topCard;
      state.hand = sortHand(data.hand);
      state.isOfferCard = true;
      state.trickScores.t1 = 0;
      state.trickScores.t2 = 0;
      state.trickNumber = 0;
      state.numRemainingCards["top"]=5;
      state.numRemainingCards["left"]=5;
      state.numRemainingCards["right"]=5;
      state.isAlone=false;
      for(let i=0; i<state.players.length; i++){
        state.players[i].tricksWon = 0;
      }

    
    } else if(data.action === "PASS"){
      state.whosTurn = parseInt(data.seatNum, 10)%4 + 1;



    } else if(data.action === "DEALER_PASS"){
      state.whosTurn = parseInt(data.seatNum, 10)%4 + 1;
      if(state.isOfferCard){
        state.isOfferCard = false;
        state.isChooseTrump = true;
      }

    } else if(data.action === "ORDER_UP"){  
      state.isDealerDiscard = true;
      state.isOfferCard = false;
      state.whosMaker = parseInt(data.seatNum,10);
      state.trump = state.offerCard.suit;
      state.isAlone = data.isAlone === "true";
      
      if(state.whosDealer === state.mySeat){
        if(state.isAlone && (state.whosMaker === (state.mySeat+1)%4 + 1)){
          store.dispatch({
            type:"DEALER_DISCARD",
            card:state.offerCard.id
          })
        } else {
          state.hand.push(state.offerCard);
        }
      }
      state.hand = sortHand(state.hand,state.trump);
      state.sortingHand = true;
      setTimeout(()=>{store.dispatch({type:"DONE_SORTING"})}, 1000);
      
      state.whosTurn = state.whosDealer;

    } else if(data.action === "CALL_TRUMP"){  
      state.whosMaker = parseInt(data.seatNum, 10);
      state.trump = data.trump;
      state.hand = sortHand(state.hand, data.trump)
      state.isAlone = data.isAlone === "true";
      state.sortingHand = true;
      setTimeout(()=>{store.dispatch({type:"DONE_SORTING"})}, 1000);

    } else if(data.action === "START_TRICK"){
      state.isDealerDiscard = false
      state.isChooseTrump = false
      state.trickNumber++;
      state.whosTurn = parseInt(data.lead,10);


    } else if(data.action === "PLAY_CARD"){

      const locationMap = {1:"bottom", 2:"left", 3:"top", 4:"right"}
      data.card.cardLocation = locationMap[((parseInt(data.seatNum, 10) - state.mySeat + 4) % 4) + 1];

      state.numRemainingCards[data.card.cardLocation]--;
      state.trickCards.push(data.card);

      if(state.trickCards.length === 1){
        if(data.card.face === "J"){
          state.suitLed = isCardTrump(data.card, state.trump) ? state.trump : data.card.suit
        } else{
          state.suitLed = data.card.suit
        }
      }
      if(parseInt(data.seatNum, 10) === state.mySeat){
        for(let i=0;i<state.hand.length;i++){
          if(state.hand[i].id === data.card.id){
              state.hand.splice(i, 1)
          }
        }
      }
      const cardsLimit = state.isAlone ? 3 : 4;
      if(state.trickCards.length < cardsLimit){
        state.whosTurn = parseInt(data.seatNum, 10)%4 + 1;
      }

    } else if(data.action === "SKIP_TURN"){
      state.whosTurn = parseInt(data.seatNum, 10)%4 + 1;


    } else if(data.action === "WIN_TRICK"){
      for(let i=0; i<state.trickCards.length; i++){
        if(state.trickCards[i].id === data.card){
          state.trickCards[i].isWinner = true;
        }
      }
      state.trickScores.t1 = parseInt(data.trickScore.t1,10)
      state.trickScores.t2 = parseInt(data.trickScore.t2,10)
      state.suitLed = null;
      state.hasPlayedCard = false;
      state.players[data.winner-1].tricksWon++
      state.whosTurn = null;
      
      setTimeout(() => {
        store.dispatch({
          type:"WIN_TRICK_ANIMATION",
          winner:parseInt(data.winner,10)
        })
      },400);

    } else if(data.action === "WIN_HAND"){
      state.gameScores.t1 = parseInt(data.gameScore.t1, 10)
      state.gameScores.t2 = parseInt(data.gameScore.t2, 10)
      state.whosMaker = "";
      state.trump = null;

    } else if(data.action === "WIN_GAME"){
      state.afterGame = true;
      state.gameStarted = false;
      state.trickNumber = 0;
      state.winString = state.gameScores.t1 >= 10 ? 
                state.players[0].name + " and " + state.players[2].name + " are the winners!" :
                state.players[1].name + " and " + state.players[3].name + " are the winners!" ;

    } else if(data.action === "RESTART_GAME"){
      state.afterGame = false;
      state.gameStarted = true;
      state.trickScores.t1 = 0;
      state.trickScores.t2 = 0;
      state.gameScores.t1 = 0;
      state.gameScores.t2 = 0;

    } else if(data.action === "CHAT_MESSAGE"){
      state.chats.push({name:data.name, message:data.message})
      if(!state.isChatOpen){
        state.newChat = true
      }
    }
    return state;
  }
  // ----------------------------------------
  //     End of Incoming Socket Activity
  // ----------------------------------------



    







  else {
    return state;
  }
}


const initialState = {

  socket:null,

  loggedIn:false,
  inGame:false,
  afterGame:false,
  lobbies:[],
  players:[{},{},{},{}],

  mySeat:null,
  myName:"",
  hand:[],

  gameStarted:false,
  whosDealer:0,
  whosTurn:0,
  whosMaker:0,

  isOfferCard: false,
  offerCard:{},
  isChooseTrump: false,
  isDealerDiscard:false,
  isAlone:false,

  trickScores:{
    t1:0,
    t2:0
  },
  gameScores:{
    t1:0,
    t2:0
  },
  numRemainingCards:{
    left:0,
    top:0,
    right:0
  },

  suitLed:null,
  trickCards:[],
  trickNumber:0,
  hasPlayedCard:false,
  trump: null,
  sortingHand:false,
  winnerLocation:null,

  isChatOpen:false,
  chats:[],
  newChat:false,

  winString:""
}


const store = createStore(reducer, initialState)




export {store}


