import React, { Component } from 'react';
import './App.css';
import {Grid, Row, Col, Button, Panel, FormGroup, FormControl, ControlLabel} from "react-bootstrap"
import {store} from "./flux.js"

class Welcome extends Component {
  constructor(){
    super();

    this.logIn = this.logIn.bind(this)
    this.handleNameChange = this.handleNameChange.bind(this)
  }

  logIn(event){
    event.preventDefault();
    if(store.getState().myName.length < 1){
      alert("Please Enter a Name");
      return;
    }
    store.dispatch({
      type:"LOG_IN"
    })
  }

  handleNameChange(event){
    if(event.target.value.length < 16){
      store.dispatch({
        type:"PLAYER_NAME_CHANGE",
        name:event.target.value
      });
    }
  }


  render(){
    const state = store.getState();
    return(
      <Panel header="Online Euchre" className="login-panel">
        <form onSubmit={this.logIn}>
          <FormGroup controlId="createLobby">
            <ControlLabel>Enter Your Name:</ControlLabel>
            <FormControl
              type="text"
              value={state.myName}
              onChange={this.handleNameChange}
            />
          </FormGroup>
          <Button type="submit" block>Play Cards</Button>
        </form>
      </Panel>
    )
  }
}


class MainMenu extends Component {

  constructor(){
    super()
    this.state = {
      createOpen:true,
      joinOpen:true,
      newLobbyName:"",
      joinLobbyName:"",
    }
    this.createLobby = this.createLobby.bind(this)
    this.joinLobby = this.joinLobby.bind(this)
  }

  createLobby(event){
    event.preventDefault();
    store.dispatch({
      type:"CREATE_LOBBY",
      lobbyName:this.state.newLobbyName
    })
  }


  joinLobby(event){
    event.preventDefault();
    store.dispatch({
      type:"SIT_DOWN",
      seatNum:event.target.value
    })
  }


  render(){
    const state = store.getState();
    
    const lobbiesMarkup = state.lobbies.map((lobby, i) => 
      <SingleLobby key={i} name={lobby.name} players={lobby.players} />
    )

    return(
      <Grid id="main-menu">
        <Row >
          <Col xs={12}>

              
            <Panel header="Create Lobby">
              <form onSubmit={this.createLobby}>
                <FormGroup controlId="createLobby">
                  <ControlLabel>Lobby Name</ControlLabel>
                  <FormControl
                    type="text"
                    value={this.state.newLobbyName}
                    onChange={(e) => this.setState({newLobbyName:e.target.value})}
                  />
                </FormGroup>

                <Button type="submit" block>Create Lobby</Button>
              </form>

            </Panel>
          </Col>
        </Row>
          
        <Row >
          <Col xs={12}>
              <Panel header="Join Lobby">
                  {lobbiesMarkup}
                  {state.lobbies.length < 1 && (
                    <p>No Lobbies Available. Create a new lobby to play.</p>
                  )}
              </Panel>
          </Col>

        </Row>
      </Grid>
    )
  }
}


class SingleLobby extends Component {
  constructor(props){
    super(props)
    this.joinLobby = this.joinLobby.bind(this)
  }
  joinLobby(event){
    store.dispatch({
      type:"SIT_DOWN",
      seatNum:event.target.value,
      lobbyName:this.props.name
    })
  }
  render(){
    let playerButtons = new Array(4);
    for(let i=0; i<playerButtons.length;i++){
      playerButtons[i] = <Button key={i} value={i+1} onClick={this.joinLobby} className="join-button">Join</Button>
    }
    for(let i=0; i<this.props.players.length;i++){
      playerButtons[this.props.players[i].seat-1] = <span className="lobby-player">{this.props.players[i].name}</span>
    }

    return (
      <Panel className="single-lobby">
        <h4>{this.props.name}</h4>
        <Row>
          <Col sm={5}>
            {playerButtons[0]}
            {playerButtons[2]}
          </Col>
          <Col sm={2}>
            <h5>VS.</h5>
          </Col>
          <Col sm={5}>
            {playerButtons[1]}
            {playerButtons[3]}
          </Col>
        </Row>
      </Panel>
    )
  }
}




export {MainMenu, Welcome}


