import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import {store} from "./flux.js";
import {Button, ButtonGroup, FormGroup, FormControl} from "react-bootstrap"

class Chat extends Component {

	constructor(){
		super()
		this.state = {
			message:""
		}
		this.sendMessage = this.sendMessage.bind(this)
		this.handleKeyDown = this.handleKeyDown.bind(this)
	}

	scrollToBottom() {
	  const node = ReactDOM.findDOMNode(this.messagesEnd);
	  node.scrollIntoView({ behavior: "smooth" });
	}

	componentDidUpdate() {
	  this.scrollToBottom();
	}
	sendMessage(event){
		event.preventDefault();
		store.dispatch({
			type:"CHAT_MESSAGE",
			message:this.state.message
		})
		this.setState({message:""})
	}
	handleKeyDown(event){
		//if we press enter without shift, submit the message
		if(event.keyCode === 13 && event.shiftKey === false) {
     this.sendMessage(event);
    }
	}
	render(){
		const state = store.getState();
		const isOpen = state.isChatOpen;
		const newChat = state.newChat;
		const openClass = isOpen ? "open" : "";

		const messages = state.chats.map((chat, i)=>{
			return <ChatItem key={i} name={chat.name} message={chat.message} />
		})

		return(
			<div id="chat" className={openClass}>
				
				<div className="chat-header">
					<div className="clearfix">
						<h4 className="pull-left">Euchre Chat</h4>
						<Button onClick={() => store.dispatch({type:"TOGGLE_CHAT"})} id="close-chat-btn" className="pull-right black-btn">
		          Close
		        </Button>
					</div>
				</div>

				<div id="chat-messages">
					<div className="display-table">
						<div className="content">
							{messages.length < 1 && (
								<p>No messages.</p>
							)}
							{messages}
							
						</div>
					</div>
					<div style={{ float:"left", clear: "both" }}
             ref={(el) => { this.messagesEnd = el; }} />
				</div>


				<div id="chat-input">
					<form onSubmit={this.sendMessage}>
	          <FormGroup>
	            <FormControl
	              componentClass="textarea"
	              placeholder="Type a message"
	              value={this.state.message}
	              onKeyDown={this.handleKeyDown}
	              onChange={(event) => this.setState({"message":event.target.value})}
	              id="chat-textarea"
	            />
	          </FormGroup>
	          <Button className="black-btn"type="submit" block>Send</Button>
	        </form>

				</div>
			</div>
		)
	}
}

class ChatItem extends Component {
	render(){
		return (
			<div className="single-chat">
				<strong>{this.props.name}</strong><br/>
				{this.props.message.split("\n").map((text,i) => {
					if(!text){
            return <br key={i}/>;
					} else{
            return <div key={i}>{text}</div>;
					}
        })}
			</div>
		)
	}
}



export {Chat};



