import React, { Component } from 'react';
import {store} from './flux.js';
import {Button} from  "react-bootstrap"
import './App.css';
import {suitSymbols, isCardPlayable, sortHand} from './euchreHelpers.js';







class PlayerContainer extends Component {
	constructor(props){
		super(props);
		this.sitDown = this.sitDown.bind(this);
	}

	sitDown(event){
		store.dispatch({
			type:"SIT_DOWN",
			seatNum:this.props.seatNum
		})
	}

  render(){
  	const state = store.getState();
  	const isPlayer = Object.keys(this.props.player).length > 0 ? true : false;
  	const isMaker = state.whosMaker === this.props.player.seat ? true : false;
  	const isDealer = state.whosDealer === this.props.player.seat ? true : false;
  	const isUser = state.mySeat === this.props.player.seat ? true : false;
  	const trumpChipStyle = state.trump === "H" || state.trump === "D" ? {"color":"red"} : {"color":"black"}
  	const playerClass = state.whosTurn === this.props.player.seat ? "player-container player-turn" : "player-container";

    return (
    	<div>
	  		{isPlayer && (
	  			<div className={playerClass}>
	  				<div className="player-container-header">
	  					{state.trickNumber > 0 && !state.afterGame && (
	  						<div className="tricks-won"><span>{this.props.player.tricksWon}</span></div>
	  					)}
	  					{this.props.player.name}
  						{isMaker && !state.afterGame && (<div className="trump-chip"><span className="suit-symbol" style={trumpChipStyle}>{suitSymbols[state.trump]}</span></div>)}
  						{isDealer && !state.afterGame && (<div className="dealer-chip"><span>D</span></div>)}
	  				</div>
	  				
	  				{isUser && (
	  					<UserPlayer player={this.props.player} location="bottom"/>
	  				)}
						{!isUser && (
	  					<OtherPlayer player={this.props.player} location={this.props.location} />
	  				)}
	  				{state.isAlone && isMaker && !state.afterGame && (
							<span>ALONE</span>
						)}
	  			</div>
	  		)}
    	</div>
    )
  }
}

class UserPlayer extends Component {
	constructor(props){
		super(props)
		this.handlePlayCard = this.handlePlayCard.bind(this);
	}
	
	handlePlayCard(card, event){
		const state = store.getState();
		if(state.whosDealer === state.mySeat && state.isDealerDiscard){
			store.dispatch({
	      type:"DEALER_DISCARD",
	      card:card
	    })
		} else if(state.whosTurn === this.props.player.seat && state.trickNumber > 0 && !state.hasPlayedCard)	{
			const cardLimit = state.isAlone ? 3 : 4;
			if(state.trickCards.length < cardLimit){
				store.dispatch({
	      	type:"PLAY_CARD",
	      	card:card
	    	})
			}
		}
	}

	render(){
		const state = store.getState();
  	const hand = state.mySeat === this.props.player.seat ? state.hand : [];
  	const playerClass = (state.whosTurn === this.props.player.seat && (state.trickNumber > 0 || state.isDealerDiscard)) ? "player-up" : "";
  	const isSorting = state.sortingHand;
  	const isPartnerAlone = state.isAlone && (state.whosMaker === (state.mySeat+1)%4 + 1)
  	const cardLimit = state.isAlone ? 3 : 4;

  	let anyPlayable = false;
  	if(state.isDealerDiscard && state.whosDealer === state.mySeat){
  		for(let i=0; i<hand.length;i++){
	  		hand[i].isPlayable = true;
	 		}
  	} else if(!state.hasPlayedCard){
  		//if we havent played a card yet for this trick, set playable cards
	  	for(let i=0; i<hand.length;i++){
	  		const playable =  isCardPlayable(hand[i], state.suitLed, state.trump);
	  		if(state.trickCards.length < cardLimit){
	  			hand[i].isPlayable = playable;
	  		}
	  		if (playable){
	  			anyPlayable = true;
	  		}
	  	}
	  	// no playable cards? then play anything
	  	if(!anyPlayable){
	  		for(let i=0; i<hand.length;i++){
	  			hand[i].isPlayable = true;
	  		}
	  	}
  	} else {
  		//if we've already played, no playable cards
  		for(let i=0; i<hand.length;i++){
	  		hand[i].isPlayable = false;
	 		}
  	}



    let cards = hand.map((card) =>
    	<Card id={card.id} key={card.id} face={card.face} suit={card.suit} onCardClick={this.handlePlayCard} isPlayable={card.isPlayable} isSorting={isSorting}/>
    )

		return(
      <div className = {playerClass}>
      	{!isPartnerAlone && (
        	<div className="hand" >{cards}</div>
      	)}
      </div>
		)
	}
}


class OtherPlayer extends Component {
	render(){
		const state = store.getState();
		const numCards = state.numRemainingCards[this.props.location];
  	
		let cards=[]
		for(let i=0;i<numCards;i++){
			cards.push(<Card key={i} flipped />)
		}
		return (
			<div className="hand">
					{cards}
			</div>
		)
	}
}



class Card extends Component {

	constructor(props){
		super(props);
		this.handleClickCard = this.handleClickCard.bind(this)
		this.state = {
			animate:false,
			styles: this.props.cardStyle ? this.props.cardStyle : {}
		}
	}
	componentDidMount(){
    if(typeof this.props.cardLocation === 'string'){
    	this.setState({animate:true});
    	// setTimeout(() => this.setState({animate:false}), 1000)
    }
	}
	handleClickCard(event){
		if(this.props.onCardClick && this.props.isPlayable){
			this.props.onCardClick(this.props.id, event)
		}
	}


  render(){
    
    let textStyle = this.props.suit === "S" || this.props.suit === "C" ? {color:"black"} : {color:"red"};
    let cardClass = this.props.isPlayable ? "card playable" : "card";
    this.props.isWinner === true ? cardClass+= " winner" : cardClass += "";
    this.props.flipped ? cardClass += " flipped" : cardClass += " face-up";
    this.props.isSorting === true ? cardClass+=" sorting" : cardClass+="";
    this.state.animate ? cardClass += " animate" : cardClass += "";
    if(typeof this.props.cardLocation === 'string'){
    	switch (this.props.cardLocation)  {
			    case "bottom":
			    	cardClass += " playedBy-1"
			      break;
			    case "left":
			    	cardClass += " playedBy-2"
			      break;
			    case "top":
			    	cardClass += " playedBy-3"
			      break;
			    case "right":
			    	cardClass += " playedBy-4"
			      break;
			    default:
			    	break;
			}
    }

    return (
      <div style={this.state.styles} className={cardClass} onClick={this.handleClickCard}>
      	{this.props.flipped && (
      		<div className="flipped-inner">
      		</div>
      	)}
      	{!this.props.flipped && (
        	<p style={textStyle}>{this.props.face}<span className="suit-symbol">{suitSymbols[this.props.suit]}</span></p>
      	)}
      </div>
    )
  }
}




export {Card, PlayerContainer}


