function makeDeck(){
  let deck = [];
  let faceMap = {0: "9", 1:"10", 2:"J", 3:"Q", 4:"K", 5:"A"}
  let suitMap = {0: "S", 1:"H", 2:"C", 3:"D"}
  for(let i=0; i<Object.keys(suitMap).length; i++){
    for(let j=0; j<Object.keys(faceMap).length; j++){
      deck.push({id:(i*6 + j), face:faceMap[j], suit:suitMap[i]});
    }
  }
  return deck;
}

const suitNames = {
  "S" : "Spades",
  "H" : "Hearts",
  "C" : "Clubs",
  "D" : "Diamonds"
}
const suitSymbols = {
  "S" : String.fromCharCode(9824),
  "H" : String.fromCharCode(9829),
  "C" : String.fromCharCode(9827),
  "D" : String.fromCharCode(9830)
} 
const faceNames = {
  "9" : "Nine",
  "10": "Ten",
  "J": "Jack",
  "Q": "Queen",
  "K": "King",
  "A": "Ace"
}


function isCardPlayable(card, suitLed, trump){
  if(!suitLed) return true;


  if(card.face === "J" && card.suit !== trump){
    if(!isCardTrump(card, trump)){
      if(card.suit === suitLed) return true;
      else return false;
    } else {
      switch (suitLed){
        case "C":
          if(card.suit === "S") return true;
          else return false;
        case "S":
          if(card.suit === "C") return true;
          else return false;
        case "D":
          if(card.suit === "H") return true;
          else return false;
        case "H":
          if(card.suit === "D") return true;
          else return false;
        default: return false
      }
    }
  } else if(card.suit === suitLed){
    return true;
  } else return false;
}


function isCardTrump(card, trump){
  if(card.face === "J" && card.suit !== trump){
    switch (trump){
      case "C":
        if(card.suit === "S") return true;
        else return false;
      case "S":
        if(card.suit === "C") return true;
        else return false;
      case "D":
        if(card.suit === "H") return true;
        else return false;
      case "H":
        if(card.suit === "D") return true;
        else return false;
      default: return false
    }
  } else if(card.suit === trump){
    return true;
  } else return false;
}

function getCardValue(card, trump){
  if(isCardTrump(card, trump)){
    if(card.face === "9") return 7
    else if(card.face === "10") return 8
    else if(card.face === "Q") return 9
    else if(card.face === "K") return 10
    else if(card.face === "A") return 11
    else if(card.face === "J"){
      if(!card.suit === trump) return 12
      else return 13
    }
  } else{
    if(card.face === "9") return 1
    else if(card.face === "10") return 2
    else if(card.face === "J") return 3
    else if(card.face === "Q") return 4
    else if(card.face === "K") return 5
    else if(card.face === "A") return 6
  }
}

function sortHand(hand, trump){
  let tempHand = { "S":[], "D":[], "C":[], "H":[] }
  for(let i=0; i<hand.length; i++){
    if(isCardTrump(hand[i],trump)){
      tempHand[trump].push(hand[i])
    } else {
      tempHand[hand[i].suit].push(hand[i])
    }
  }
  for(let suit in tempHand){
    if(tempHand[suit].length){
      tempHand[suit] = orderCardsByValue(tempHand[suit], trump)
    }
  }

  if(trump){
    let final = tempHand[trump].slice();
    tempHand[trump] = [];
    return final.concat(tempHand.S, tempHand.D, tempHand.C, tempHand.H)
  }else {
    return tempHand.S.concat(tempHand.D, tempHand.C, tempHand.H)
  }

}

function orderCardsByValue(cards, trump){
  let tempCards = [];
  while(cards.length > 0){
    let highIndex = 0;
    for(let i=1; i<cards.length; i++){
      if(getCardValue(cards[i],trump) > getCardValue(cards[highIndex],trump)){
          highIndex = i;
      }
    }
    tempCards.push(cards[highIndex])
    cards.splice(highIndex, 1)
  }
  return tempCards;
}



export {makeDeck, suitNames, suitSymbols, faceNames, isCardPlayable, isCardTrump, sortHand}


