import React, { Component } from 'react';
import './App.css';
import './animations.css';
import {Card, PlayerContainer} from "./components.js"
import {Welcome,MainMenu} from "./Menu.js"
import {Chat} from "./Chat.js"
import {Grid, Row, Col, Button, ButtonGroup,Checkbox, Panel, Navbar} from "react-bootstrap"
import {store} from "./flux.js"
import {suitSymbols} from "./euchreHelpers.js"



class App extends Component {
  constructor(){
    super();
    store.subscribe(() => this.forceUpdate());
  }


  render() {
    const state = store.getState();
    const loggedIn = state.loggedIn;
    const inGame = state.inGame;
    const appClass = state.isChatOpen ? "chat-open" : "";
    const usScore = (state.mySeat === 1 || state.mySeat === 3) ? state.gameScores.t1 : state.gameScores.t2;
    const themScore = (state.mySeat === 1 || state.mySeat === 3) ? state.gameScores.t2 : state.gameScores.t1;
    const isNewChat = state.newChat;
    return (
      <div id="App" className={appClass}>
        
        {!loggedIn && (
          <Welcome />
        )}
        {loggedIn && (
          <EuchreNavbar playerName={state.myName} inGame={inGame} usScore={usScore} themScore={themScore} isNewChat={isNewChat}/>
        )}
        {loggedIn && !inGame && (
          <MainMenu />
        )}

        {loggedIn && inGame && (
          <Euchre state={state}/>
        )}
        

        {loggedIn && (
          <Chat />
        )}

      </div>
    );
  }
}


class EuchreNavbar extends Component {
  render (){
    const buttonClass = this.props.isNewChat ? "pull-right btn-info" : "pull-right black-btn";
    return (
      <Navbar fluid>
        <Navbar.Brand>
          Euchre
        </Navbar.Brand>
        {!this.props.inGame &&(
          <Navbar.Text className="pull-left">
            Signed in as: {this.props.playerName}
          </Navbar.Text>
        )}
        {this.props.inGame &&(
          <Navbar.Text className="pull-left">
            <span style={{"paddingRight":"20px"}}>Us: {this.props.usScore}</span>Them: {this.props.themScore}
          </Navbar.Text>
        )}
        <Button onClick={() => store.dispatch({type:"TOGGLE_CHAT"})} className={buttonClass}>
          Chat
        </Button>
      </Navbar>
    )
  }
}



class Euchre extends Component{
  constructor(){
    super();
    this.handleStartGame = this.handleStartGame.bind(this)
    this.restartGame = this.restartGame.bind(this)
    this.backToMenu = this.backToMenu.bind(this)
  }

  componentDidMount() {
    store.subscribe(() => this.forceUpdate());
  }

  handleStartGame(){
    store.dispatch({type:"START_GAME"})
  }

  restartGame(){
    store.dispatch({
      type:"RESTART_GAME",
    })
  }

  backToMenu(){
    store.dispatch({
      type:"BACK_TO_MENU",
    })
  }
  

  render(){
    const state = store.getState();
    const playerOffset = state.mySeat ? state.mySeat - 1 : 0;

    return(
      <Grid id="main-grid">
        
        <Row id="top-row">
          <Col xs={3} >
           
          </Col>
          <Col xs={6} >
            <PlayerContainer player={state.players[(2+playerOffset)%4]} seatNum={3} location="top"/>
          </Col>

          <Col xs={3}>
            
          </Col>
        </Row>



        <Row id="middle-row">
          <Col xs={3}>
            <PlayerContainer player={state.players[(1+playerOffset)%4]} seatNum={2} location="left"/>
          </Col>

          <Col xs={6} className="middle-col display-table">
            <div className="content">
              {!state.gameStarted  && (
                <div>
                  {state.afterGame && (
                    <div>
                      <p>{state.winString}</p>
                      <ButtonGroup>
                        <Button onClick={this.restartGame}>Restart Game</Button>
                        <Button onClick={this.backToMenu}>Back to Menu</Button>
                      </ButtonGroup>
                    </div>
                  )}
                  {!state.afterGame && (
                    <Button onClick={this.handleStartGame}>Start Game</Button>
                  )}
                </div>
              )}
              {state.gameStarted && (
                <GameArea />
              )}
            </div>
          </Col>

          <Col xs={3}  >
            <PlayerContainer player={state.players[(3+playerOffset)%4]} seatNum={4} location="right"/>  
          </Col>
        </Row>




        <Row id="bottom-row">

          <Col xs={3} className="xs-hidden">
          </Col>

          <Col xs={6} id="user-container">
            <PlayerContainer player={state.players[playerOffset]} seatNum={1} location="bottom"/>
          </Col>

          <Col xs={3} className="xs-hidden">
          </Col>
        </Row>




      </Grid>
    )
  }
}


class GameArea extends Component {
  constructor(props){
    super(props)
    this.orderUp = this.orderUp.bind(this)
    this.pass = this.pass.bind(this)
    this.chooseTrump = this.chooseTrump.bind(this)
    this.state = {
      selectedTrump:undefined,
      trumpChosen:false,
    }
  }


  pass(){
    store.dispatch({
      type:"PASS",
    })
  }

  orderUp(event){
    store.dispatch({
      type:"ORDER_UP",
      isAlone:event.target.value
    })
    //setTimeout is a kludge to stop the trump buttons from displaying for a short time
    setTimeout(()=> this.setState({trumpChosen:false}), 200)
    
  }

  chooseTrump(event){
    store.dispatch({
      type:"CALL_TRUMP",
      trump:this.state.selectedTrump,
      isAlone:event.target.value
    })
    //setTimeout is a kludge to stop the trump buttons from displaying for a short time
    setTimeout(()=> this.setState({trumpChosen:false}), 200)
  }



  render(){
    const state = store.getState();
    const myTurn = state.whosTurn === state.mySeat ? true : false;
    const trumpChosen = this.state.trumpChosen;

    const trickCards = state.trickCards.map((card) =>
      <Card key={card.id} id={card.id} face={card.face} suit={card.suit} cardLocation={card.cardLocation} isPlayable={false} isWinner={card.isWinner}/>
    )

    let trumpButtons = [];
    const suitMap = {0: "S", 1:"H", 2:"C", 3:"D"}
    for(let i = 0; i<Object.keys(suitMap).length; i++){
      if(state.offerCard.suit !== suitMap[i]){
        const buttonStyle = suitMap[i] === "H" || suitMap[i] === "D" ? {"color":"red"} : {"color":"black"}
        trumpButtons.push(<Button onClick={()=> this.setState({selectedTrump:suitMap[i], trumpChosen:true})}  key={suitMap[i]} style={buttonStyle} className="trump-button">{suitSymbols[suitMap[i]]} </Button>)
      }
    }

    let playerUp = {};
    for(let i=0; i<state.players.length; i++){
      if(state.whosTurn === state.players[i].seat){
        playerUp = state.players[i]
      }
    }
    let trickCardsClassName = "";
    if(state.winnerLocation){
      trickCardsClassName = "animate animate-" + state.winnerLocation;
    }


    return (
      <div>

        {state.isOfferCard && (
          <div> 
            
            <p>{playerUp.name} - Order up card?</p>
            <Card key={state.offerCard.id} face={state.offerCard.face} suit={state.offerCard.suit} isPlayable={false}/><br/>

            {myTurn && (
              <div className="trump-buttons-group">
                {!trumpChosen && (
                  <ButtonGroup>
                    <Button className="trump-button" onClick={()=> this.setState({trumpChosen:true})}>Order Up</Button>
                    <Button className="trump-button" onClick={this.pass}>Pass</Button>
                  </ButtonGroup>
                )}

                {trumpChosen && (
                  <div>
                    <ButtonGroup>
                      <Button value={false} onClick={this.orderUp}>With Partner</Button>
                      <Button value={true} onClick={this.orderUp}>Go Alone</Button>
                    </ButtonGroup><br/>
                    <Button onClick={() => this.setState({trumpChosen:false})}>Back</Button>
                  </div>
                )}
              </div>
            )}

          </div>
        )}

        {state.isDealerDiscard && (state.whosDealer === state.mySeat) && (
          
          <p>Select a card to discard</p>
        )}

        {state.isDealerDiscard && (state.whosDealer !== state.mySeat) && (
          <p>Dealer Discarding...</p>
        )}


        {state.isChooseTrump && (
          <div> 
            <p>{playerUp.name} - Choose Trump?</p>
            
            {myTurn && (
              <div className="trump-buttons-group">
                {!trumpChosen && (
                  <ButtonGroup>
                    {!(state.whosDealer === state.mySeat) && (
                      <Button onClick={this.pass} className="trump-button">Pass</Button>
                    )}
                    {trumpButtons}
                  </ButtonGroup>
                )}
                {trumpChosen && (
                  <div>
                    <ButtonGroup>
                      <Button value={false} onClick={this.chooseTrump}>With Partner</Button>
                      <Button value={true} onClick={this.chooseTrump}>Go Alone</Button>
                    </ButtonGroup><br/>
                    <Button onClick={() => this.setState({trumpChosen:false})}>Back</Button>
                  </div>
                )}
              </div>
            )}

          </div>
        )}

        {state.trickNumber > 0 && (
          <div id="trickCards" className={trickCardsClassName}> 
            {trickCards}
          </div>
        )}




      </div>

    )
  }
}


class ScoreBoard extends Component {
  render(){
    const state = store.getState();
    return (
      <Panel header="Score" className="scoreboard">
        <div className="scoreboard-team clearfix">
          <div className="pull-left">
            {state.players[0].name}<br/> 
            {state.players[2].name}
          </div>  
          <div className="pull-right scoreboard-score">
            {state.gameScores.t1}
          </div>  
        </div>
        
        <div className="scoreboard-team clearfix">
          <div className="pull-left">
            {state.players[1].name}<br/> 
            {state.players[3].name}
          </div>  
          <div className="pull-right scoreboard-score">
            {state.gameScores.t2}
          </div>
        </div>
      </Panel>
    )
  }
}


class DealerDiscard extends Component {
  constructor(){
    super();
    this.handleDiscard = this.handleDiscard.bind(this)
  }

  handleDiscard(cardId){
    store.dispatch({
      type:"DEALER_DISCARD",
      card:cardId
    })
  }

  render(){
    const state = store.getState();

    const cardsMarkup = state.hand.map((card) =>
      <Card onCardClick={this.handleDiscard} id={card.id} key={card.id} face={card.face} suit={card.suit} isPlayable={true}/>
    )
    if(Object.keys(state.offerCard).length>0){
      cardsMarkup.push(
        <Card cardStyle={{"marginLeft":"20px"}} onCardClick={this.handleDiscard} id={state.offerCard.id} key={state.offerCard.id} face={state.offerCard.face} suit={state.offerCard.suit} isPlayable={true}/>
      )
    }
    return (
      <div className="dealer-discard">
        <p>Discard 1 Card</p>
        {cardsMarkup}
      </div>
    )
  }
}



export default App;



