import {store} from "./flux.js"



function createSocket(url, protocol){
	let socket = new WebSocket(url, protocol);

	const send = (data) => socket.send(data);

	const onopen = (func) => socket.onopen = func

	socket.onmessage = (event) => {
	    try {
	      let jsonData = JSON.parse(event.data)
		    if(!jsonData.action){
		      throw new Error("No action field in socket message... cancelling message");
		    } else {
		    	// console.log(jsonData)
				store.dispatch({
					type:"SOCKET_MESSAGE",
					data:jsonData
				})
			}
	    } catch (e){
	     
	    }
	}

	return {
		socket,
		send,
		onopen
	}
}




 export {createSocket}


